.site-footer{background-color:  #84A178;


@media screen and (max-width: 767px) {
    padding-bottom: 20px;
    padding-top: 20px;
    }


@media screen and (min-width: 768px) {
    padding-bottom: 12px;
    padding-top: 40px;
    }


@media screen and (min-width: 1200px) {
padding-bottom: 12px;
padding-top: 60px;
}

}

.site-footer__block{
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and(max-width:767px) {
        flex-direction: column;
        margin-bottom: 60px;
    }
    
    @media screen and (min-width: 768px) {
        margin-bottom: 40px;
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 120px;

    }

}

.site-footer__block--position{
   
    justify-content: space-between;
    

    @media screen and(max-width:767px) {
        flex-wrap: wrap;
        align-items: self-start;
    }


}

.site-footer__titles{
    display: flex;
    flex-direction: column;
    margin-bottom: 60px;
    



}

.site-footer__contacts{display: flex;
    flex-direction: column;
}

.footer-button{
    display: inline-block;
    border-radius: 10px;
    
    color: rgba(19, 19, 19, 1);
    font-weight: 600;
    font-size: 14px;
    line-height: 1.28px;
    padding-top: 12px;
    padding-bottom: 12px;
    width: 196px;
    height: 42px;
    margin: 0;
    border: 2px solid #F8D24E;

    background: transparent;
    cursor: pointer;
  

    // @media screen and (min-width:120px) {
    //     margin-bottom: 120px;
    // }
//  margin-top: 60px;
//  
}

    .footer-button__block {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
                align-items: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                
    }

    .footer-button--color{
        background-color: #F8D24E; 

        @media screen and (max-width:767px) {
            margin-bottom: 16px;
        }   
     @media screen and (min-width:768px) {
        margin-right: 16px;
     }

      
    
    }

    .footer-button__text{margin-right: 7px;}

    .site-footer__pretitle{
       
        font-weight: 400;
        font-size: 30px;
        line-height: 1.23;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        
        color: #FCFCFC;

        
        @media screen and (max-width:767px) {
            font-weight: 700;
            font-size: 18px;
            line-height: 1.22;
            margin-bottom: 16px;
            max-width: 232px;

        }


        @media screen and (min-width:768px) {
            font-weight: 700;
            font-size: 26px;
           line-height: 1.23;
           margin-bottom: 8px;
           max-width: 352px;
        }

        @media screen and (min-width:1200px) {
            font-weight: 400;
            font-size: 30px;
            line-height: 1.23;
            margin-bottom: 14px;
            min-width: 504px;
        }

        
    }

    .site-footer__title{ 



letter-spacing: 0.06em;
text-transform: uppercase;
color: #FCFCFC;
font-weight: 700;

@media screen and (max-width:767px) {
    font-size: 34px;
    line-height: 1.21;
    max-width: 200px;
}
@media screen and (min-width:768px) {
    font-size: 48px;
    line-height: 1.23;
    max-width: 352px;
}
@media screen and (min-width:1200px) {
font-size: 58px;
line-height: 1.23;
min-width: 494px;
}

    }

    .foot-social{ 
        display: flex;
        list-style: none;
        justify-content: flex-end;
        gap: 30px;
    }

    .foot-social__contact{
       
        font-weight: 700;
        color:  #131313;
        outline: 1px solid orange;
                       
        @media screen and (max-width:767px) {
            font-size: 12px;
            line-height: 1.83;
            margin-top: 20px;
        }
        @media screen and (min-width:768px) {
            font-size: 14px;
            line-height: 1.86;
            margin-top: 34px;
        }
        @media screen and (min-width:1200px) {
            font-size: 16px;
            line-height: 1.88;
            margin-top: 40px;
        }

    }

    .foot-social__mobile{color: #FCFCFC;
    text-decoration: none;
    
    }

    .foot-social__link{
        width: 100%;
        height: 100%;
        outline: 1px solid orange;
        
        
        // .foot-social__link:last-child{margin-right: 0px;}
    }

    // .foot-social__item{margin-right: 30px;}
    // .foot-social__item:last-child{margin-right: 0px;}

    .site-footer__copyright{
    display: block;

font-family: 'Montserrat';
font-style: normal;

letter-spacing: 0.02em;
color: #7A7A7A;
text-align: center;
border-top: 1px solid #e1e1e1 ;
font-weight: 700;




@media screen and (max-width:767px) {
    font-size: 14px;
    line-height: 1.5;
   
}
@media screen and (min-width:768px) {
    font-size: 14px;
    line-height: 1.86;
}
@media screen and (min-width:1200px) {
    font-size: 12px;
    line-height: 1.83;
}





    }


