.backdrop {
  position: relative;
}

.modal {
  display: block;
  width: 100vw;
  height: 100vh;
  padding-left: 28px;
  padding-right: 28px;
  background: #fcfcfc;
  border-radius: 10px;
}

.modal__title {
  margin-bottom: 20px;
  padding-top: 28px;
  font-family: $titleFontFamily;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;

  // display: flex;
  // align-items: center;
  text-align: center;
  color: $textColor;
}

.modal__text {
  margin-bottom: 24px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: $textColor;
}

.modal__name,
.modal__tel,
.modal__email,
.modal__comment {
  width: 100%;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;

  padding-left: 12px;
  padding-top: 8px;
  padding-bottom: 8px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.4;
  color: $textColor;
}

.product .modal__name,
.product .modal__tel {
  margin-bottom: 12px;
}
.product .modal__comment {
  margin-bottom: 26px;
}
.modal__submit {
  display: block;
  min-width: 114px;
  height: 42px;
  margin: 0 auto;
  padding: 0 auto;
  background: #f8d24e;
  border-radius: 10px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  // display: flex;
  // align-items: center;
  text-align: center;
  color: $textColor;
}

.modal__btn--closed {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 20px;
  height: 20px;
}
